// Servio para gerenciar notificaes do navegador
class NotificationService {
  constructor() {
    this.permission = null;
    this.scheduledNotifications = new Map();
    this.checkNotificationSupport();
  }

  // Verificar se o navegador suporta notificaes
  checkNotificationSupport() {
    if (!('Notification' in window)) {
      console.warn('Este navegador no suporta notificaes');
      return false;
    }
    this.permission = Notification.permission;
    return true;
  }

  // Solicitar permisso para enviar notificaes
  async requestPermission() {
    if (!this.checkNotificationSupport()) {
      return false;
    }

    if (this.permission === 'granted') {
      return true;
    }

    if (this.permission === 'denied') {
      console.warn('Permisso para notificaes foi negada');
      return false;
    }

    try {
      const permission = await Notification.requestPermission();
      this.permission = permission;
      return permission === 'granted';
    } catch (error) {
      console.error('Erro ao solicitar permisso para notificaes:', error);
      return false;
    }
  }

  // Enviar notificao imediata
  async sendNotification(title, options = {}) {
    if (!(await this.requestPermission())) {
      return false;
    }

    const defaultOptions = {
      icon: '/favicon.ico',
      badge: '/favicon.ico',
      vibrate: [200, 100, 200],
      requireInteraction: true,
      tag: 'habit-reminder'
    };

    const notification = new Notification(title, { ...defaultOptions, ...options });

    // Auto-fechar aps 10 segundos se no for interativa
    if (!options.requireInteraction) {
      setTimeout(() => notification.close(), 10000);
    }

    return notification;
  }

  // Agendar notificao para um horrio especfico
  scheduleNotification(habitId, habitName, time) {
    // Cancelar notificao anterior se existir
    this.cancelNotification(habitId, time);

    const now = new Date();
    const [hours, minutes] = time.split(':').map(Number);
    
    const notificationTime = new Date();
    notificationTime.setHours(hours, minutes, 0, 0);

    // Se o horrio j passou hoje, agendar para amanh
    if (notificationTime <= now) {
      notificationTime.setDate(notificationTime.getDate() + 1);
    }

    const delay = notificationTime.getTime() - now.getTime();
    const notificationKey = `${habitId}-${time}`;

    const timeoutId = setTimeout(() => {
      this.sendNotification(
        `Lembrete: ${habitName}`,
        {
          body: `Hora de ${habitName.toLowerCase()}! No se esquea de completar seu hbito.`,
          icon: '/favicon.ico',
          tag: `habit-${habitId}-${time}`,
          data: { habitId, habitName, time }
        }
      );

      // Reagendar para o prximo dia
      this.scheduleNotification(habitId, habitName, time);
    }, delay);

    this.scheduledNotifications.set(notificationKey, timeoutId);

    console.log(`Lembrete agendado para ${habitName} s ${time}`);
    return timeoutId;
  }

  // Agendar mltiplos horrios para um hbito
  scheduleMultipleNotifications(habitId, habitName, times) {
    // Cancelar todas as notificaes do hbito primeiro
    this.cancelHabitNotifications(habitId);
    
    // Agendar cada horrio
    times.forEach(time => {
      this.scheduleNotification(habitId, habitName, time);
    });
  }

  // Cancelar notificao agendada especfica
  cancelNotification(habitId, time = null) {
    if (time) {
      const notificationKey = `${habitId}-${time}`;
      const timeoutId = this.scheduledNotifications.get(notificationKey);
      if (timeoutId) {
        clearTimeout(timeoutId);
        this.scheduledNotifications.delete(notificationKey);
        console.log(`Lembrete cancelado para hbito ${habitId} s ${time}`);
      }
    } else {
      // Cancelar todas as notificaes do hbito (compatibilidade com verso anterior)
      this.cancelHabitNotifications(habitId);
    }
  }

  // Cancelar todas as notificaes de um hbito
  cancelHabitNotifications(habitId) {
    const keysToDelete = [];
    this.scheduledNotifications.forEach((timeoutId, key) => {
      if (key.startsWith(`${habitId}-`)) {
        clearTimeout(timeoutId);
        keysToDelete.push(key);
      }
    });
    
    keysToDelete.forEach(key => {
      this.scheduledNotifications.delete(key);
    });
    
    if (keysToDelete.length > 0) {
      console.log(`${keysToDelete.length} lembretes cancelados para hbito ${habitId}`);
    }
  }

  // Agendar todos os lembretes ativos
  scheduleAllReminders(habits) {
    habits.forEach(habit => {
      if (habit.reminder_enabled) {
        if (habit.reminder_times && habit.reminder_times.length > 0) {
          // Usar mltiplos horrios se disponvel
          this.scheduleMultipleNotifications(habit.id, habit.name, habit.reminder_times);
        } else if (habit.reminder_time) {
          // Fallback para horrio nico
          this.scheduleNotification(habit.id, habit.name, habit.reminder_time);
        }
      }
    });
  }

  // Cancelar todos os lembretes
  cancelAllReminders() {
    this.scheduledNotifications.forEach((timeoutId, habitId) => {
      clearTimeout(timeoutId);
    });
    this.scheduledNotifications.clear();
    console.log('Todos os lembretes foram cancelados');
  }

  // Verificar status da permisso
  getPermissionStatus() {
    return this.permission;
  }

  // Testar notificao
  async testNotification() {
    return await this.sendNotification(
      'Teste de Notificao',
      {
        body: 'As notificaes esto funcionando corretamente!',
        requireInteraction: false
      }
    );
  }
}

// Instncia singleton
const notificationService = new NotificationService();

export default notificationService;
